<template>
<!--归档组件-->
  <div class="block">
    <el-timeline>
      <el-timeline-item
          color="#0bbd87" :timestamp="item.updateTime" placement="top"
          :key="index" v-for="(item,index) in AllArticle"
          >
        <el-card>
          <span class="title">{{item.description}}</span>
          <p style="margin-top: 10px">
            <i class="el-icon-date" ></i> {{ item.updateTime }}
            <i class="el-icon-view" style="margin-left: 15px"></i>{{ item.views }}
          </p>
        </el-card>
      </el-timeline-item>
    </el-timeline>

  </div>    
</template>

<script>
  export default {
    name:'timeLocus',
    inject: ['reload'],
    data() {
      return {
        // 获取文章数组
        AllArticle:[{
          id: '1',//文章id
          description: '',//文章摘要
          updateTime: "2021-10-27 14:39:25",//发布时间
          views: 15,//浏览量
        }],
      }
    },
    methods:{
        // 获取所有文章
      GetAllArticle(){
        this.$http.get('/blog/archive').then(res => {
            if(res.data.result ===true){
              // 获取文章数组
              this.AllArticle =res.data.data.archiveInfo
            }
        })
      },
    },
    created(){
      this.GetAllArticle();
    },
  }
</script>

<style lang="scss" scoped>
.title {
  font-size: 16px;
  font-weight: 600;
}
.content {
  margin: 20px 0;
}
</style>